import React from 'react';
import Img from 'gatsby-image';
import { useStaticQuery, graphql } from 'gatsby';

import classes from './BackgroundImageContainer.module.scss';

const BackgroundImageContainer = ({ putAtBottom }) => {
  const data = useStaticQuery(graphql`
    query {
      mobileImage: file(relativePath: { eq: "1-cut-out.png" }) {
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid_withWebp_tracedSVG
          }
        }
      }
      desktopImage: file(relativePath: { eq: "1.jpeg" }) {
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid_withWebp_tracedSVG
          }
        }
      }
    }
  `);

  let imgStyle = {
    display: 'block',
    position: 'absolute',
    top: 0,
    right: 0,
    left: 'initial',
    height: '100%',
    width: 'auto',
    mixBlendMode: 'soft-light',
    objectFit: 'initial',
    objectPosition: 'initial',
  };

  if (putAtBottom) {
    imgStyle = {
      ...imgStyle,
      top: 'initial',
      bottom: 0,
      width: '80%',
      height: 'auto',
    };
  }

  return (
    <Img
      className={classes.ImageContainer}
      style={{ position: 'absolute' }}
      imgStyle={imgStyle}
      fluid={
        putAtBottom
          ? data.mobileImage.childImageSharp.fluid
          : data.desktopImage.childImageSharp.fluid
      }
      alt=""
    />
  );
};

export default BackgroundImageContainer;
