import React from 'react';

import BackgroundImageContainer from '../../containers/BackgroundImageContainer/BackgroundImageContainer';

import classes from './Background.module.scss';

const background = ({ putImgBottom }) => {
  const imageHolderClassList = [classes.BackgroundImageHolder];
  const foregroundColorClassList = [classes.BackgroundForegroundColor];

  if (putImgBottom) {
    imageHolderClassList.push(classes.ImageHolderModified);
    foregroundColorClassList.push(classes.ForegroundColorModified);
  }

  return (
    <div className={classes.Background}>
      <div className={imageHolderClassList.join(' ')}>
        <BackgroundImageContainer putAtBottom={putImgBottom} />
      </div>
      <div className={foregroundColorClassList.join(' ')}></div>
    </div>
  );
};

export default background;
